body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.PlayListPage {
    background: url(/playlistdemo/static/media/bg.e4bafef5.jpg);
    background-size: 100vw 100vh;
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Panel {
    width: 70vw;
    height: 70vh;
    background-color: rgba(191, 191, 191, 0.90);
    border-radius: 1vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 5vw;
}

.PanelList {
    width: 70vw;
    height: 70vh;
    background-color: rgba(191, 191, 191, 0.749);
    border-radius: 1vw;
    padding: 5vw;
    overflow: scroll;
}

::-webkit-scrollbar {
    display: none;
}

.Button {
    cursor: pointer;
    width: 80%;
    height: 10vh;
    border-top: 1px solid #96d1f8;
    background: #65a9d7;
    background: -o-linear-gradient(top, #3e779d, #65a9d7);
    border-radius: 40px;
    box-shadow: rgba(0, 0, 0, 1) 0 1px 0;
    text-shadow: rgba(0, 0, 0, .4) 0 1px 0;
    color: white;
    font-size: 2vw;
    font-family: 'Lucida Grande', Helvetica, Arial, Sans-Serif;
    text-decoration: none;
    vertical-align: middle;
    padding: 1vw;
    margin: 1vw;
}

.Button:hover {
    border-top-color: #28597a;
    background: #28597a;
    color: #ccc;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s; /* Animation */
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.Button:active {
    border-top-color: #1b435e;
    background: #1b435e;
}

.Button:focus {
    outline: none;
}

.TextInput {
    width: 85%;
    height: 6vh;
    border-radius: 50px;
    margin: 1vw;
    padding: 1vw;
    font-size: 2vw;
    text-align: center;
}

:focus {
    outline: none;
}

.ButtonStart {
    cursor: pointer;
}

.ButtonStart:hover {
    border-top-color: #74c7ff;
    background: #3e90c7;
    color: #ccc;
    box-shadow: #ccc;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s; /* Animation */
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.ButtonStart.active {
    border-top-color: #2d94d8;
    background: #064772;
    color: #ccc;
    box-shadow: #ccc;

}

.VideoPlayer {
    width: 100%;
    height: 100%;
}

.hide {
    display: none;
}

.controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    width: 53.5vw;
    padding: 0.5vw;
    border-radius: 0.4vw;
    background: rgba(30, 30, 30, .75);
    visibility: hidden;
    opacity: 0;
    transition: all .25s ease-out;
}

.VideoPlayer:hover > .controls {
    visibility: visible;
    opacity: 1;
}

.play_pause_btn {
    outline: none;
    border: none;
    border-radius: 0.4vw;
    cursor: pointer;
    padding: 0.5vw 1.3vw;
    transition: all .15s ease-out;
    background: rgba(203, 133, 137, .5);

&
:hover {
    background: rgba(203, 133, 137, .75);
}

}

.time {
    color: #fff;
    font-size: 1vw;
}

.volume_range {
    width: 5vw !important;
}

input[type=range] {
    -webkit-appearance: none;
    width: 19vw;
    margin: 1vw;
    background: none;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 1vh;
    cursor: pointer;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
    background: #796465;
    border-radius: 0.3vw;
}

input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
    border: 0px solid rgba(0, 0, 0, 0);
    height: 3vh;
    width: 0.8vw;
    border-radius: 0.4vw;
    background: #eeeeee;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -1vh;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: #877071;
}

input[type=range]::-moz-range-track {
    width: 100%;
    height: 1vh;
    cursor: pointer;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
    background: #796465;
    border-radius: 1.9px;
    border: 0px solid rgba(1, 1, 1, 0);
}

input[type=range]::-moz-range-thumb {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
    border: 0px solid rgba(0, 0, 0, 0);
    height: 2vh;
    width: 10vw;
    border-radius: 0.4vw;
    background: #eeeeee;
    cursor: pointer;
}

input[type=range]::-ms-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
}

input[type=range]::-ms-fill-lower {
    background: #6b5859;
    border: 0px solid rgba(1, 1, 1, 0);
    border-radius: 3.8px;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
}

input[type=range]::-ms-fill-upper {
    background: #796465;
    border: 0px solid rgba(1, 1, 1, 0);
    border-radius: 3.8px;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
}

input[type=range]::-ms-thumb {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
    border: 0px solid rgba(0, 0, 0, 0);
    height: 20px;
    width: 10px;
    border-radius: 4px;
    background: #eeeeee;
    cursor: pointer;
    height: 8px;
}

input[type=range]:focus::-ms-fill-lower {
    background: #796465;
}

input[type=range]:focus::-ms-fill-upper {
    background: #877071;
}

